import { useEffect, useState } from "react"
import { useLocation, useNavigate } from "react-router-dom"
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from 'swiper/modules';
import "./style.css"
import { useDispatch, useSelector } from "react-redux"
import { addToCart } from "../../store/actions/AuthActions"
import { useTranslation } from "react-i18next"
import Select from 'react-select';
import Loader from "../../common/Loader"
import { toast } from "react-toastify"
import ProductsService from "../../services/ProductsService"
import CartService from "../../services/CartService";
import ProductCard from "../../Enums/ProductCard";

const Product = () => {
    const [product, setProduct] = useState({})
    const [similarProduct, setSimilarProduct] = useState([])
    const [amount, setAmount] = useState(1)
    const [selectedImage, setSelectedImage] = useState('')
    const [variants, setVariants] = useState([])
    const [variantsIds, setVariantsIds] = useState([])
    const [fixedIds, setFixedIds] = useState([])
    const location = useLocation()
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const {t} = useTranslation()
    const [found, setFound] = useState(true)
    const [loader, setLoader] = useState(false)
    const [loading, setLoading] = useState(false)
    const [custom, setCustom] = useState(false)
    const [categoryId, setCategoryId] = useState(null)
    const [shouldUpdate, setShouldUpdate] = useState(false)
    const lang = useSelector(state => state?.lang?.lang)
    const user = useSelector(state => state?.user)
    const productsService = new ProductsService()
    const cartService = new CartService()

    useEffect(()=>{
        let id = location?.pathname?.split('/product/')[1]
        setLoading(true)
        productsService.getProdust(id)?.then(res=>{
            if(res?.status === 200){
                setProduct(res?.data?.data?.product)
                setSelectedImage(res?.data?.data?.product.product_images[0]?.url)
                setCategoryId(res?.data?.data?.product?.category_id)
                if(res?.data?.data?.variant?.length > 0) {
                    setVariants(res?.data?.data?.variant)
                    let ids = res?.data?.data?.variant?.map( variant => variant?.variant_values?.find(val => val?.isSelected).id )
                    setVariantsIds(ids)
                    setFixedIds(ids)
                }
                setFound(true)
            }
            setLoading(false)
        }).catch((e)=> console.error(e))
    },[location])

    useEffect(()=>{
        if(!!categoryId){
            let params ={
                category_id: categoryId,
                offset: 0,
                limit: 15
            }
            productsService?.getList(params).then(res=>{
                if(res?.status){
                    setSimilarProduct(res?.data?.data?.data?.filter(prod => prod?.id !== product?.id))
                }
            })
        }
    }, [categoryId])

    useEffect(()=>{
        if(custom){
                productsService.changeProduct(product?.code, variantsIds.map(id => `variant_value_ids=${id}`).join('&'))?.then(res=>{
                    if(res?.status === 200){
                        setProduct(res?.data?.data?.product)
                        setSelectedImage(res?.data?.data?.product.product_images[0]?.url)
                        if(res?.data?.data?.variant?.length > 0) {
                            setVariants(res?.data?.data?.variant)
                            let ids = res?.data?.data?.variant?.map( variant => variant?.variant_values?.find(val => val?.isSelected).id )
                            setVariantsIds(ids)
                            setFixedIds(ids)
                        }
                        setFound(true)
                    }
                }).catch(e=> {
                    // setVariantsIds(fixedIds)
                    if(e.response?.data?.message === 'product_not_Exist'){
                        setFound(false)
                        return
                    }
                    toast.error(e.response?.data?.message?.replaceAll('_', ' '))
                })
        }
    },[shouldUpdate])

    const addCart = () => {
        let data = {
            products: [{
                amount: amount,
                product_id: product?.id
            }]
        }
        setLoader(true)
        if(!!user?.user){
            cartService.create(data).then(res=>{
                if(res?.status === 201){
                    toast.success(t("Product Added To Cart"));
                    dispatch(addToCart({
                        ...product,
                        amount: amount
                    }))
                }
                setLoader(false);
            }).catch(e=> {
                setLoader(false);
                if(e.response?.data?.message === "product_is_already_Exist_in_your_cart"){
                    let data ={
                        product_id: product.id,
                        amount: amount
                    }
                    cartService.update(data).then(res=>{
                        if(res?.status === 200){
                            toast.success(t("Product Added To Cart"));
                        }
                    })
                    return
                }
                toast.error(e.response?.data?.message?.replaceAll('_', ' '))
            })
        } else {
            toast.success(t("Product Added To Cart"));
            dispatch(addToCart({
                ...product,
                in_stock: product?.location_of_products[0]?.amount,
                amount: amount
            }))
            setTimeout(()=> setLoader(false), [500])
        }
    }

    // if(loading){
    //     return <Loader />
    // }
    return <>
        <div className="product">
            <div className="row mb-5">
                    <div className="col-md-5 h-100 text-center">
                        <img src={selectedImage} alt='product' className="w-75 main-img" />
                        <div className="px-5">
                        <Swiper
                            slidesPerView={4}
                            spaceBetween={5}
                            pagination={{
                            clickable: true,
                            }}
                            className="mySwiper mt-4 d-flex flex-column"
                            style={{direction: 'ltr'}}
                            modules={[Pagination]}
                        >
                            {product?.product_images
                            ?.filter(res => !res?.url?.endsWith('.pdf') && !res?.url?.endsWith('.doc') && !res?.url?.endsWith('.docx'))
                            ?.map((img,index)=>{
                                return <SwiperSlide style={{height: 'auto'}} key={index}>
                                    <img
                                        className="w-75 h-100"  
                                        src={img?.url} 
                                        alt={index} 
                                        onClick={()=> setSelectedImage(img?.url)}
                                    />
                                </SwiperSlide>
                            })}
                        </Swiper>
                        </div>
                    </div>
                    <div className="col-md-7">
                        <h1 className="product-name">{lang==='en' ? product?.name_en : product?.name_ar}</h1>
                        <h2 className="product-category">{lang==='en' ? product?.category?.name_en : product?.category?.name_ar}</h2>
                        <div className='prices'>
                            {!product?.offer &&<p className="price">{product.price_one?.toFixed(3)} {t("KWD")}</p>}
                            {product?.offer && <p className="price">{product.offerPrice?.toFixed(3)} {t("KWD")}</p>}
                            {product?.offer && <p className="old-price">{product.price_one?.toFixed(3)} {t("KWD")}</p>}
                            {product.offer && <p className="discount">-{(product.price_one-product.offerPrice)?.toFixed(3)} {t("KWD")}</p>}
                        </div>
                        {!!product?.product_images?.filter(res => res?.url?.endsWith('.pdf') || res?.url?.endsWith('.doc') || res?.url?.endsWith('.docx'))?.length &&
                        <a 
                            href={product?.product_images?.find(res => res?.url?.endsWith('.pdf') || res?.url?.endsWith('.doc') || res?.url?.endsWith('.docx'))?.url}
                            target="_blank" rel="noreferrer"
                            style={{
                                textDecoration: 'none',
                                fontSize: '18px',
                                fontWeight: '600',
                                gap: '4px'
                            }}
                            className="text-primary d-flex mb-3"
                        >
                             Link
                        </a>}
                        {!!product.brand_id && <div className='brand'>
                            <p>{t("Brand")}:</p>
                            <img src={product?.brand?.image} alt='brand' />
                        </div>}
                        <div className='description'>
                            <p>{t("Description")}:</p>
                            <span>
                                {lang==='en' ? product.description_en : product.description_ar}
                            </span>
                        </div>
                        {variants?.map((variant, index)=> {
                            return <div className="variant mb-4" key={index}>
                                <p className="mb-1">{lang==='en' ? variant?.name_en : variant?.name_ar}</p>
                                <div className="variant-values"
                                style={{gridTemplateColumns: variant?.name_ar === '' ? "auto auto auto auto auto" : "auto auto auto"}}>
                                    {(variant?.name_ar === '' || variant?.name_en === 'color') ? variant?.variant_values?.map((val, ind)=> {
                                        return <div className="value position-relative mb-4" key={ind}>
                                        <span 
                                            onClick={()=> {
                                                setCustom(true)
                                                setShouldUpdate(prev => !prev)
                                                let update = variantsIds?.map((id, i) => {
                                                    if(i === index){
                                                        return val?.id
                                                    } else {
                                                        return id
                                                    }
                                                })
                                                variants?.map((res, ind)=>{
                                                    if(ind === index){
                                                        const objOlde = res?.variant_values.find((item) => item.isSelected);
                                                        const objToUpdate = res?.variant_values.find((item) => item.id === val.id);
                                                        objOlde.isSelected = false;
                                                        objToUpdate.isSelected = true;
                                                    } 
                                                    return res
                                                })
                                                setVariantsIds(update)
                                            }}
                                            style={{
                                            position: 'absolute', cursor: 'pointer',
                                            width: '30px', height: '30px',
                                            border: '1px solid #dedede',
                                            backgroundColor: val?.value_en
                                            }}>
                                            {val?.isSelected && <span style={{
                                            position: 'absolute', borderRadius: '50%',
                                            width: '15px', height: '15px',
                                            border: '2px solid #dedede',
                                            backgroundColor: '#fff',
                                            top: '50%',  left: '50%',
                                            transform: 'translate(-50%, -50%)'
                                        }}></span>}
                                        </span>
                                        </div>
                                    }) :
                                    <>
                                    <Select
                                        value={{
                                            ...variant?.variant_values?.find(v=> v.isSelected),
                                            label: lang==='en' ? variant?.variant_values?.find(v=> v.isSelected).value_en : variant?.variant_values?.find(v=> v.isSelected).value_ar,
                                            value: variant?.variant_values?.find(v=> v.isSelected).value_en,
                                        }}
                                        placeholder={t("Select")}
                                        options={variant?.variant_values?.map(val=> {
                                            return {
                                                ...val,
                                                label: lang==='en' ? val?.value_en : val?.value_ar,
                                                value: val?.value_en
                                            }
                                        })}
                                        onChange={(e)=> {
                                            let update = variantsIds?.map((id, i) => {
                                                    if(i === index){
                                                        return e?.id
                                                    } else {
                                                        return id
                                                    }
                                                })
                                            variants?.map((res, ind)=>{
                                                if(ind === index){
                                                    const objOlde = res?.variant_values.find((item) => item.isSelected);
                                                    const objToUpdate = res?.variant_values.find((item) => item.id === e.id);
                                                    objOlde.isSelected = false;
                                                    objToUpdate.isSelected = true;
                                                } 
                                                return res
                                            })
                                            setCustom(true)
                                            setVariantsIds(update)
                                            setShouldUpdate(prev => !prev)
                                        }}
                                    />
                                    </>}
                                </div>
                            </div>
                        })}
                        {!!product?.location_of_products?.length && <div className="amounts d-flex" style={{alignItems: 'center'}}>
                                <button
                                    onClick={()=> {
                                        setAmount(prev=> --prev)
                                    }}
                                    className="btn btn-outline-secondary" 
                                    disabled={amount === 1}
                                >-</button>
                                <span style={{
                                    marginRight: '12px',
                                    marginLeft: '12px'
                                }}>{amount}</span>
                                <button 
                                    disabled={product?.location_of_products[0]?.amount === amount}
                                    style={{cursor: product?.location_of_products[0]?.amount === amount ? 'not-allowed' : 'pointer'}}
                                    onClick={()=> setAmount(prev => ++prev)}
                                    className="btn btn-outline-secondary" 
                                >+</button>
                            </div>}
                        {(!!product?.location_of_products?.length && product?.location_of_products[0]?.amount > 0) && <div className="d-flex" style={{gap: '22px'}}>
                            {loader && <div className='d-flex justify-content-center' style={{width: '167px'}}><Loader /></div>}
                            {(!loader && found )&& <button onClick={()=> addCart()} className="buy">{t("Add To Cart")}</button>}
                        </div>}
                        {((!!product?.location_of_products?.length && product?.location_of_products[0]?.amount === 0) || !found) && <p className="text-danger mt-4">{t("out of stock")}</p>}
                    </div>
            </div>
        </div>
        {!!similarProduct?.length && <div className="similar_products">
            <h1>{t("Similar Products")}</h1>
            <div>
            <Swiper
                slidesPerView={3}
                spaceBetween={10}
                pagination={{
                clickable: true,
                }}
                modules={[]}
                className="mySwiper"
                breakpoints={{
                    640: {
                      slidesPerView: 3,
                      spaceBetween: 10,
                    },
                    768: {
                      slidesPerView: 4,
                      spaceBetween: 15,
                    },
                    1024: {
                      slidesPerView: 4,
                      spaceBetween: 25,
                    },
                  }}
            >
                {similarProduct?.map((item, index) => {
                return <SwiperSlide key={index} onClick={()=> navigate(`/product/${item?.id}`)}>
                    <ProductCard product={item} hasAddToCart={false} />
                </SwiperSlide>
                })}
            </Swiper>
            </div>
        </div>}
    </>
}
export default Product