import { useEffect, useState } from 'react';
import bg from '../../../assets/bg.jpeg';
import whats from '../../../assets/icons/whats.svg';
import arrowLeft from '../../../assets/icons/arrowLeft.svg';
import arrowRight from '../../../assets/icons/arrowRight.svg';
import BannerService from '../../../services/BannerService';
import { useTranslation } from 'react-i18next';
import './style.css'

function Header() {
    const [data, setData] = useState([])
    const bannerService = new BannerService()
    const { t } = useTranslation();

    useEffect(()=>{
      bannerService?.getList().then(res=>{
        if(res?.status === 200){
          let data = !!res?.data?.data?.length ? res?.data?.data[0]?.image : ""
          setData(data)
        }
      }).catch(()=> {})
    },[])

  return (
    <div className='header-home'>
      <img src={data || bg} alt="slide" className="d-block bg" />
      <div className='buttons'>
        <button className='fun'>
          <img src={arrowLeft} alt='arrowLeft' />
          Idea Fun
          {/* {t("Join Us")} */}
        </button>
        <button className='learn'>
          {/* {t("Join Us")} */}
          Idea Learn
          <img src={arrowRight} alt='arrowRight' />
        </button>
      </div>
      <div className='icons'>
        <img src={whats} alt='whats' />
        <svg width="64" height="64" viewBox="0 0 64 64" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect width="64" height="64" rx="32" fill="#F58539"/>
          <path fill-rule="evenodd" clip-rule="evenodd" d="M21.3675 15.9995C13.9827 15.9995 7.99609 21.9861 7.99609 29.3709C7.99609 34.4733 10.854 38.9082 15.0563 41.1622L13.2301 47.1357C13.1731 47.3221 13.3833 47.4759 13.5437 47.3651L20.2966 42.7001C20.6499 42.7281 21.007 42.7424 21.3675 42.7424H42.6247C50.0095 42.7424 55.9961 36.7558 55.9961 29.3709C55.9961 21.9861 50.0095 15.9995 42.6247 15.9995H21.3675Z" fill="#FDFDFE"/>
          <path d="M18.2816 27.829C18.2816 29.8172 21.4581 31.3773 22.9676 31.3773C24.4771 31.3773 24.1398 29.8172 24.1398 27.829C24.1398 25.8408 22.5242 24.229 21.0147 24.229C19.5053 24.229 18.2816 25.8408 18.2816 27.829Z" fill="#F58539"/>
          <path d="M46.3981 27.829C46.3981 29.8172 43.2216 31.3773 41.7121 31.3773C40.2026 31.3773 40.5398 29.8172 40.5398 27.829C40.5398 25.8408 42.1555 24.229 43.6649 24.229C45.1744 24.229 46.3981 25.8408 46.3981 27.829Z" fill="#F58539"/>
        </svg>
      </div>
    </div>
  );
}

export default Header;