import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import AboutService from '../../services/AboutService';
import bg from "../../assets/bg.jpeg"
import students from "../../assets/icons/students.svg"
import flash from "../../assets/icons/flash.svg"
import './style.css'

const AboutUs = () => {
  const {t} = useTranslation()
  const [about, setAbout] = useState([])
  const aboutService = new AboutService()

  useEffect(()=>{
    let params = {
      type: "about" 
    }
  
    aboutService?.getList(params).then(res=>{
      if(res?.status === 200){
        if(!!res?.data?.data?.length) setAbout(res?.data?.data[0])
      }
    })
  }, [])

  return (<div className='aboutUs'>
    <div className='about-header'>
      <img src={bg} alt='bg' />
      <h5 className='header-title'>
        {t("about-us")}
      </h5>
    </div>
    <div className='container'>
      <p className='about-title'>{about?.title}</p>
      <p className='about-description'>{about?.description}</p>
      {(!!about?.total_active_students || !!about?.available_programs_and_camps || !!about?.available_educational_courses) && <div className='details'>
        {!!about?.total_active_students && <div className='det-info students'>
          <div>
            <img src={students} alt='students' />
            <span>{formatNumber(Number(about?.total_active_students))} +</span>
          </div>
          <p>{t("Total active students and increasing")}</p>
        </div>}
        {!!about?.available_programs_and_camps && <div className='det-info programs'>
          <div>
            <img src={flash} alt='programs' />
            <span>{formatNumber(Number(about?.available_programs_and_camps))} +</span>
          </div>
          <p>{t("Available programs and camps")}</p>
        </div>}
        {!!about?.available_educational_courses && <div className='det-info courses'>
          <div>
            <img src={flash} alt='courses' />
            <span>{formatNumber(Number(about?.available_educational_courses))} +</span>
          </div>
          <p>{t("Available Educational Courses")}</p>
        </div>}
      </div>}
    </div>
  </div>);
};

const formatNumber = (num) => {
  if (num >= 1_000_000) {
    return (num / 1_000_000).toFixed(1) + " M";
  } else if (num >= 1_000) {
    return (num / 1_000).toFixed(1) + " K";
  } else {
    return num.toString();
  }
};

export default AboutUs;
