import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import arrowIcon from "../../assets/icons/arrow-right.svg"
import './style.css'

const CategoryCard = ({item, type}) => {
  const lang = useSelector(state => state?.lang?.lang)

  // return <Link to={`/category/${category.id}`} key={category?.id} style={{textDecoration: 'none'}} className='category-card'>
  return <Link to={`/${type}/${item?.id}`} state={item} key={item?.id} style={{textDecoration: 'none'}} className='category-card'>
    <img src={type === "gallery" ? item?.banner_image : item?.image} alt='item' className='main-img w-100' />
    <div className='information'>
      <h1 className='title'>{type === "gallery" ? item?.title : item?.name_en}</h1>
      <img src={arrowIcon} alt='arrow-icon' className='arrowIcon' />
    </div>
  </Link>
}
export default CategoryCard