import React, { useEffect, useState } from 'react';
import CategoryCard from '../../Enums/CategoryCard';
import { useTranslation } from 'react-i18next';
import GalleryService from '../../services/GalleryService';
import './style.css'

const Gallery = () => {
  const [data, setData] = useState([])
  const [loading, setLoading] = useState(false)
  const {t} = useTranslation()

  useEffect(()=>{
    setLoading(true)
    new GalleryService().getList().then(res=>{
      if(res?.status === 200){
        let info = res?.data?.data?.data
        setData(info)
      }
      setLoading(false)
    }).catch(()=> setLoading(false))
  },[])

  return <div className='categories container'>
    <h5 className='type-title'>{t("gallery")}</h5>
    {!loading && <div className='products p-0'>
      <div className='row'>
        {!!data?.length && data?.map((item, index)=>{
          return <div className="col-md-4 col-6 mb-4" key={index}>
                <CategoryCard item={item} type='gallery' />
              </div>
            })}
          </div>
    </div>}
    {/* {loading && <Loader />} */}
  </div>
};

export default Gallery;
