import BaseService from "./BaseService";
import http from './HttpService'
import { API_BASE_URL_ENV } from "../common/common";

const apiEndpoint = API_BASE_URL_ENV() + "/cms/contactUs";
const apiRequestEndpoint = API_BASE_URL_ENV() + "/cms/joinRequest";
const apiSuggestionEndpoint = API_BASE_URL_ENV() + "/cms/request";

export default class ContactUsService extends BaseService {
    constructor() {
        super(apiEndpoint);
    }

    joinRequest(data){
        return http.post(apiRequestEndpoint, {...data})
    }

    suggestionRequest(data){
        return http.post(apiSuggestionEndpoint, {...data})
    }
}
