import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { AvField, AvForm } from 'availity-reactstrap-validation';
import ContactUsService from '../../services/ContactUsService';
import { toast } from 'react-toastify';
import Select from "react-select"
import chat from "../../assets/icons/chat.svg"
import message from "../../assets/icons/message.svg"
import location from "../../assets/icons/location.svg"
import phone from "../../assets/icons/phone.svg"
import PhoneInput from "react-phone-input-2";
import BaseService from '../../services/BaseService';
import PositionsService from '../../services/PositionsService';
import './style.css'

const Contact = () => {
  const [formData, setFormData] = useState({
    f_name: '',
    l_name: '',
    job_title: '',
    gmail: '',
    position_applied: '',
    phone: '',
    cv: ''
  })
  const [contactInfo, setContactInfo] = useState({})
  const [suggestion, setSuggestion] = useState({
    title: '',
    phone: '',
    subject: ''
  })
  const tabs = ["Suggestion", "Complaint", "Inquiry"]
  const [selectedTab, setSelectedTab] = useState("Suggestion")
  const [loading, setLoading] = useState(false)
  const [loading2, setLoading2] = useState(false)
  const [positionsOptions, setPositionsOptions] = useState([])
  const {t} = useTranslation()
  const contactUsService = new ContactUsService()

  useEffect(()=> {
    contactUsService?.getList().then(res=> {
      if(res?.status){
        let info = res?.data?.data
        setContactInfo({
          ...info,
          another_call_number: info?.another_call_number || ""
        })
      }
    })
    new PositionsService().getList().then(res=> {
      if(res?.status){
        let info = res?.data?.data?.data?.map(opt=> {
          return{
            label: opt.name,
            value: opt.id,
          }
        })
        setPositionsOptions(info)
      }
    })
  }, [])

  const submit = () => {
    if(!suggestion.phone || !suggestion?.subject || !suggestion?.title) {
      toast.error(t('complete all phases'))
      return
    }

    let data = {
      type: selectedTab.toLocaleLowerCase(),
      phone: suggestion?.phone,
      title: suggestion?.title,
      subject: suggestion?.subject,
    }
    setLoading2(true)
    contactUsService.suggestionRequest(data).then(res=>{
      if(res?.status === 201){
        toast.success(t('Request Sent Successfully.'))
        setSuggestion({
          title: '',
          phone: '',
          subject: ''
        })
      }
      setLoading2(false)
    }).catch(e=> {
      setLoading2(false)
      toast.error(e?.response?.data?.message)
    })
  }

  const joinRequest = () => {
    if(!formData.position_applied || !formData.f_name || !formData.l_name || !formData.gmail || !formData.job_title) {
      toast.error(t('complete all phases'))
      return
    }
    let data = {
      f_name: formData?.f_name,
      l_name: formData?.l_name,
      gmail: formData?.gmail,
      phone: formData?.phone,
      position_id: formData?.position_applied?.value,
      job_title: formData?.job_title,
      cv: formData?.cv
    }
    setLoading(true)
    contactUsService.joinRequest(data).then(res=>{
      if(res?.status === 201){
        toast.success(t('Request Sent Successfully.'))
        setFormData({
          f_name: '',
          l_name: '',
          gmail: '',
          job_title: '',
          phone: '',
          position_applied: ''
        })
      }
      setLoading(false)
    }).catch(e=> {
      toast.error(e?.response?.data?.message)
      setLoading(false)
    })
  }

  const fileHandler = (e) => {
    let filesAll = e.target.files;
    const filesData = Object.values(filesAll);

    new BaseService().postUpload(filesData[0]).then((res) => {
        if (res?.data?.status) {
            setFormData({ ...formData, cv: res.data.url });
        }
    });
};
  return (<div className='contact'>
    <div className='text-center contact-header'>
      <p className='contact-title'>{t("Contact our friendly team")}</p>
      <p className='contact-subtitle'>{t("Let us know how we can help.")}</p>
    </div>
    <div className='container'>
      <div className='row'>
        <div className='col-md-3'>
          <div className='contact-social'>
            <img src={chat} alt="chat" />
            <p className='social-title'>{t("Chat to sales")}</p>
            <p className='social-subtitle'>{t("Speak to our friendly team")}</p>
            <p className='social-value'>{contactInfo?.sales_website}</p>
          </div>
        </div>
        <div className='col-md-3'>
          <div className='contact-social'>
            <img src={message} alt="message" />
            <p className='social-title'>{t("Chat to Support")}</p>
            <p className='social-subtitle'>{t("Were here to help.")}</p>
            <p className='social-value'>{contactInfo?.support_website}</p>
          </div>
        </div>
        <div className='col-md-3'>
          <div className='contact-social'>
            <img src={location} alt="location" />
            <p className='social-title'>{t("Visit Us")}</p>
            <p className='social-subtitle'>{t("Visit our office HQ.")}</p>
            <a href={contactInfo?.location} target='_blank' className='social-value' rel="noreferrer">
              {t("View on Google Maps")}
            </a>
          </div>
        </div>
        <div className='col-md-3'>
          <div className='contact-social'>
            <img src={phone} alt="phone" />
            <p className='social-title'>{t("Call Us")}</p>
            <p className='social-subtitle'>{contactInfo?.day_from}-{contactInfo?.day_to} from {contactInfo?.time_from} to {contactInfo?.time_to}</p>
            <div className='d-flex justify-content-between'>
              <a href={`tel:${contactInfo?.call_number}`} className='social-value'>
                +{contactInfo?.call_number}
              </a>
              {!!contactInfo?.another_call_number && <a href={`tel:${contactInfo?.another_call_number}`} className='social-value'>
                +{contactInfo?.another_call_number}
              </a>}
            </div>
          </div>
        </div>

        <div className='col-md-6'>
          <AvForm
            className='form-horizontal join-us-form'
            onValidSubmit={joinRequest}
          >
            <div className='row'>
              <div className='col-md-12'>
                <h4 className='getInTouch'>{t("Join Us")}</h4>
              </div>
              <div className='col-md-6'>
                <AvField
                  label={t("First Name")}
                  type='text'
                  placeholder={t("First Name")}
                  bsSize="lg"
                  name='f_name'
                  // validate={{
                  //     required: {
                  //         value: true,
                  //         errorMessage: `${t("This Field is required")}`
                  //     }
                  // }}
                  value={formData.f_name}
                  onChange={(e) => setFormData({...formData, f_name: e.target.value})}
                />
              </div>
              <div className='col-md-6'>
                <AvField
                  label={t("Last Name")}
                  type='text'
                  placeholder={t("Last Name")}
                  bsSize="lg"
                  name='l_name'
                  // validate={{
                  //     required: {
                  //         value: true,
                  //         errorMessage: `${t("This Field is required")}`
                  //     }
                  // }}
                  value={formData.l_name}
                  onChange={(e) => setFormData({...formData, l_name: e.target.value})}
                />
              </div>
              <div className='col-md-6'>
                <AvField
                  label={t("Job Title")}
                  type='text'
                  placeholder={t("Job Title")}
                  bsSize="lg"
                  name='job_title'
                  // validate={{
                  //     required: {
                  //         value: true,
                  //         errorMessage: `${t("This Field is required")}`
                  //     }
                  // }}
                  value={formData.job_title}
                  onChange={(e) => setFormData({...formData, job_title: e.target.value})}
                />
              </div>
              <div className='col-md-6'>
                <AvField
                  label={t("Email")}
                  type='email'
                  placeholder={t("Email")}
                  bsSize="lg"
                  name='gmail'
                  // validate={{
                  //     required: {
                  //         value: true,
                  //         errorMessage: `${t("This Field is required")}`
                  //     }
                  // }}
                  value={formData.gmail}
                  onChange={(e) => setFormData({...formData, gmail: e.target.value})}
                />
              </div>
              <div className='col-md-6 mb-3'>
                <label className="mb-2">{`${t("Position Applied")}`}</label>
                <Select
                  options={positionsOptions}
                  name='position_applied'
                  value={formData?.position_applied}
                  onChange={e=> setFormData({...formData, position_applied: e})}
                />
              </div>
              <div className='col-md-6 mb-3'>
                <label className="mb-2">{t("Phone")}</label>
                <PhoneInput
                    country={"kw"}
                    enableSearch={true}
                    placeholder="Enter Phone"
                    value={formData?.phone}
                    onChange={(e) => setFormData({...formData, phone: e})}
                />
              </div>
              <div className='col-md-12'>
                <lable>{t("Upload CV")}</lable>
                <div className="upload">
                    <input 
                        type='file' 
                        name='cv'
                        id='cv'
                        accept=".pdf,.doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                        onChange={(e)=> fileHandler(e)}
                    />
                    <p>{t("Upload your ID")}</p>
                    {!formData?.cv ? <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M16.44 8.9C20.04 9.21 21.51 11.06 21.51 15.11V15.24C21.51 19.71 19.72 21.5 15.25 21.5H8.73998C4.26998 21.5 2.47998 19.71 2.47998 15.24V15.11C2.47998 11.09 3.92998 9.24 7.46998 8.91" stroke="#0F50A6" stroke-width="1.5" strokeLinecap="round" stroke-linejoin="round"/>
                        <path d="M12 15V3.62" stroke="#0F50A6" stroke-width="1.5" strokeLinecap="round" stroke-linejoin="round"/>
                        <path d="M15.35 5.85L12 2.5L8.65002 5.85" stroke="#0F50A6" stroke-width="1.5" strokeLinecap="round" stroke-linejoin="round"/>
                        </svg>
                    : <svg width="24" height="24" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M8 1.33333C4.32667 1.33333 1.33334 4.32667 1.33334 8C1.33334 11.6733 4.32667 14.6667 8 14.6667C11.6733 14.6667 14.6667 11.6733 14.6667 8C14.6667 4.32667 11.6733 1.33333 8 1.33333ZM11.1867 6.46667L7.40667 10.2467C7.31334 10.34 7.18667 10.3933 7.05334 10.3933C6.92 10.3933 6.79334 10.34 6.7 10.2467L4.81334 8.36C4.62 8.16667 4.62 7.84667 4.81334 7.65333C5.00667 7.46 5.32667 7.46 5.52 7.65333L7.05334 9.18667L10.48 5.76C10.6733 5.56667 10.9933 5.56667 11.1867 5.76C11.38 5.95333 11.38 6.26667 11.1867 6.46667Z" fill="#21AA37"/>
                    </svg>}
                </div>
              </div>
              <div className='col-md-12'>
                <button 
                  type='submit'
                  disabled={loading}
                  className='apply'
                >{t("Apply")}</button>
              </div>
            </div>
          </AvForm>
        </div>
        <div className='col-md-6'>
          <AvForm
            className='form-horizontal suggestion-form'
            onValidSubmit={submit}
          >
            <div className='row'>
              <div className='col-md-12 contact-tabs'>
                {tabs?.map(tab => {
                  return <p
                    onClick={()=> setSelectedTab(tab)}
                    className={`tab ${tab === selectedTab ? 'active' : ''}`}
                  >{t(tab)}</p>
                })}
              </div>
              <div className='col-md-12 mb-3'>
                <label className="mb-2">{t("Phone")}</label>
                <PhoneInput
                    country={"kw"}
                    enableSearch={true}
                    placeholder="Enter Phone"
                    value={suggestion?.phone}
                    onChange={(e) => setSuggestion({...suggestion, phone: e})}
                />
              </div>
              <div className='col-md-12'>
                <AvField
                  label={t("Title")}
                  type='text'
                  placeholder={t("Title")}
                  bsSize="lg"
                  name='title'
                  // validate={{
                  //     required: {
                  //         value: true,
                  //         errorMessage: `${t("This Field is required")}`
                  //     }
                  // }}
                  value={suggestion.title}
                  onChange={(e) => setSuggestion({...suggestion, title: e.target.value})}
                />
              </div>
              <div className='col-md-12'>
                <label className="mb-2">{t("Subject")}</label>
                <textarea
                  placeholder={t("Subject")}
                  name='subject'
                  value={suggestion.subject}
                  onChange={(e) => setSuggestion({...suggestion, subject: e.target.value})}
                />
              </div>
              <div className='col-md-12'>
                <button 
                  type='submit'
                  className='apply'
                  disabled={loading2}
                >{t("confirm")}</button>
              </div>
            </div>
          </AvForm>
        </div>
      </div>
    </div>
    </div>
  );
};

export default Contact;
