import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import './style.css'

const BlogCard = ({item}) => {
  const lang = useSelector(state => state?.lang?.lang)

  return <Link to={`/blogs/${item?.id}`} state={item} key={item?.id} style={{textDecoration: 'none'}} className='blog-card'>
    <img src={item?.image} alt='item' className='main-img w-100' />
    <div className='blog-info'>
      <h1 className='title'>{item?.title?.slice(0,80)}{!!item?.title?.slice(80) ? '...' : ''}</h1>
      <p className='description'>{item?.description?.slice(0,100)}{!!item?.description?.slice(100) ? '...' : ''}</p>
      <p className='read-time'>
        {item?.read_time}
          <svg className='mx-3' width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M14.4297 5.93018L20.4997 12.0002L14.4297 18.0702" stroke="#F58539" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
            <path d="M3.5 12H20.33" stroke="#F58539" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
          </svg>
      </p>
    </div>
  </Link>
}
export default BlogCard