import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import BlogCard from '../../Enums/BlogCard';
import BlogsService from '../../services/BlogsService';
import './style.css'

const Blogs = () => {
  const [data, setData] = useState([])
  const [loading, setLoading] = useState(false)
  const {t} = useTranslation()

  useEffect(()=>{
    setLoading(true)
    new BlogsService().getList().then(res=>{
      if(res?.status === 200){
        let info = res?.data?.data?.data
        setData(info)
      }
      setLoading(false)
    }).catch(()=> setLoading(false))
  },[])

  if(loading){
    return
  }
  return <div className='categories container'>
    <h5 className='type-title'>{t("Popular Blogs")}</h5>
    {!loading && <div className='products p-0'>
      <div className='row'>
        {!!data?.length && data?.map((blog, index)=>{
          return <div className="col-md-4 col-6 mb-4" key={index}>
                <BlogCard item={blog} />
              </div>
            })}
          </div>
    </div>}
    {/* {loading && <Loader />} */}
  </div>
};

export default Blogs;
